<template>
    <section class="wrapper flex flex-wrap mt-2">
        <div v-for="(item, index) in products" :key="index" class="wrapper__item w-1/6 px-2 mb-4">
            <a href="/" class="wrapper__link h-full w-full flex flex-col items-center bg-white relative text-center">
                <div :style="url(item.thumbnail)" class="wrapper__item__img w-full h-4/6 bg-top bg-cover bg-no-repeat"></div>
                <div class="wrapper__item__bottom flex-1 flex flex-col px-4 pb-4">
                    <h3 class="wrapper__item__bottom__name flex-1 py-2 text-2xl text-left">{{ item.title }}</h3>
                    <div class="wrapper__item__bottom__extra flex items-center justify-between text-2xl pt-2">
                        <div class="wrapper__item__bottom__extra-price text-primary-color">{{ item.price }} บาท</div>
                        <div v-if="item.stock != 0" class="wrapper__item__bottom__extra-sold text-xl">ขายแล้ว {{ item.stock }}</div>
                    </div>
                </div>

                <div class="wrapper__link__label-favorite absolute">
                    <span>ขายดี</span>
                </div>
                <div class="wrapper__link__label-mall absolute bg-red-color">
                    <span>Mall</span>
                </div>
                <div v-if="item.discountPercentage != 0"
                    class="wrapper__link__label-discount absolute flex justify-center items-center flex-col bg-yellow-color">
                    <span class="text-primary-color">{{ item.discountPercentage }}%</span>
                </div>
            </a>
        </div>
    </section>
</template>

<script>
import axios from 'axios';

export default {
    name: 'ProductWrapperItem',
    data() {
        return {
            products: []
        }
    },
    created() {
        axios.get('https://dummyjson.com/products')
            .then(response => {
                this.products = response.data.products;
            })
            .catch(error => {
                console.error('There was an error fetching the data :', error);
            });
    },
    methods: {
        url: function (url) {
            return `background-image: url(${url});`;
        }
    }
}
</script>

